import { router } from "../router";

export function WelcomePage(): HTMLElement {
  const container = document.createElement("div");
  container.className = "page welcome-page";

  container.innerHTML = `
    <img
      src="/images/Piedra Papel Tijera titulo.png"
      alt="Piedra Papel Tijera"
      class="title-image"
    />

    <button class="start-button">
      <img src="/images/botón empezar.png" alt="Empezar" />
    </button>

    <div class="hands-container">
      <img src="/images/tijera.png" alt="Tijera" class="hand-icon" />
      <img src="/images/piedra.png" alt="Piedra" class="hand-icon" />
      <img src="/images/papel.png" alt="Papel" class="hand-icon" />
    </div>
  `;

  const startButton = container.querySelector(
    ".start-button"
  ) as HTMLButtonElement;
  startButton.addEventListener("click", () => {
    router.navigate("/game");
  });

  return container;
}
